.profileForm {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 100%;
}

.profileForm button.updateButtons {
  display: inline-block;
  border-radius: 52px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  padding: 10px;
}

.profileForm button.updateButton {
  background-color: #F28F17;
  max-width: 150px;
}

.profileForm button.restoreButton {
  background-color: #17bff2;
  max-width: 150px;
}

.profileForm .formFields {
  width: 75%;
}


@media (max-width: 900px) {
  .profileForm {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
  }
}
