section.profileImageChangerContainer {
  min-width: 25%;
  color: #4790da;
  font-size: 1.02em;
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  width: -moz-max-content;
}

section.profileImageChangerContainer img.profilePhoto {
  width: 158px;
  height: 158px;
  border-radius: 3px;
  background-color: white;
}
