.authenticationForm {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 100%;
  min-height: 150px;
}

.authenticationForm button.updateButtons {
  display: inline-block;
  border-radius: 52px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  padding: 10px;
}

.authenticationForm button.updateButton {
  background-color: #F28F17;
  max-width: 150px;
}

.authenticationForm button.restoreButton {
  background-color: #17bff2;
  max-width: 150px;
}

.authenticationForm .formPad {
  width: 25%;
}

.authenticationForm .formFields {
  width: 75%;
  //width: 80%;
}

@media (max-width: 900px) {
  .authenticationForm {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
    margin-bottom: 150px
  }
}
